<template>
  <div class="dashboard">
    <el-row :gutter="20">
      <!--  -->
      <el-col :span="6">
        <el-card class="user-card">
          <div class="user-info">
            <el-avatar :size="60" :src="userAvatar"></el-avatar>
            <div class="user-details">
              <h3>{{ userName }}</h3>
              <p>: {{ riskLevelText }}</p>
            </div>
          </div>
        </el-card>
      </el-col>

      <!--  -->
      <el-col :span="6">
        <el-card>
          <template #header>
            <span></span>
          </template>
          <div class="stat-card">
            <h2 style="color: #67C23A;">{{ totalAssets.toLocaleString() }}</h2>
            <p> +5.2%</p>
          </div>
        </el-card>
      </el-col>

      <!--  -->
      <el-col :span="6">
        <el-card>
          <template #header>
            <span></span>
          </template>
          <div class="stat-card">
            <h2 style="color: #E6A23C;">{{ todayProfit.toLocaleString() }}</h2>
            <p> +1.8%</p>
          </div>
        </el-card>
      </el-col>

      <!--  -->
      <el-col :span="6">
        <el-card>
          <template #header>
            <span></span>
          </template>
          <div class="stat-card">
            <h2 style="color: #409EFF;">{{ recommendedProducts }}</h2>
            <p></p>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <el-row :gutter="20" style="margin-top: 20px;">
      <!--  -->
      <el-col :span="12">
        <el-card>
          <template #header>
            <span></span>
          </template>
          <div ref="assetChart" style="height: 300px;"></div>
        </el-card>
      </el-col>

      <!--  -->
      <el-col :span="12">
        <el-card>
          <template #header>
            <span></span>
          </template>
          <div ref="profitChart" style="height: 300px;"></div>
        </el-card>
      </el-col>
    </el-row>

    <el-row :gutter="20" style="margin-top: 20px;">
      <!--  -->
      <el-col :span="24">
        <el-card>
          <template #header>
            <span></span>
          </template>
          <el-tabs v-model="activeTab">
            <el-tab-pane label="" name="funds">
              <el-table :data="recommendedFunds" style="width: 100%">
                <el-table-column prop="name" label="" width="200"></el-table-column>
                <el-table-column prop="code" label="" width="120"></el-table-column>
                <el-table-column prop="type" label="" width="120"></el-table-column>
                <el-table-column prop="risk_level" label="" width="100">
                  <template #default="scope">
                    <el-tag :type="getRiskTagType(scope.row.risk_level)">
                      {{ scope.row.risk_level }}
                    </el-tag>
                  </template>
                </el-table-column>
                <el-table-column prop="expected_return" label="" width="120">
                  <template #default="scope">
                    {{ (scope.row.expected_return * 100).toFixed(2) }}%
                  </template>
                </el-table-column>
                <el-table-column label="" width="120">
                  <template #default="scope">
                    <el-button type="primary" size="small" @click="viewFundDetail(scope.row)">
                      
                    </el-button>
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>

            <el-tab-pane label="" name="stocks">
              <el-table :data="recommendedStocks" style="width: 100%">
                <el-table-column prop="name" label="" width="200"></el-table-column>
                <el-table-column prop="code" label="" width="120"></el-table-column>
                <el-table-column prop="industry" label="" width="120"></el-table-column>
                <el-table-column prop="pe_ratio" label="" width="100"></el-table-column>
                <el-table-column prop="dividend_yield" label="" width="100">
                  <template #default="scope">
                    {{ (scope.row.dividend_yield * 100).toFixed(2) }}%
                  </template>
                </el-table-column>
                <el-table-column label="" width="120">
                  <template #default="scope">
                    <el-button type="primary" size="small" @click="viewStockDetail(scope.row)">
                      
                    </el-button>
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>

            <el-tab-pane label="" name="insurance">
              <el-table :data="recommendedInsurance" style="width: 100%">
                <el-table-column prop="name" label="" width="200"></el-table-column>
                <el-table-column prop="type" label="" width="120"></el-table-column>
                <el-table-column prop="premium" label="" width="120">
                  <template #default="scope">
                    {{ scope.row.premium.toLocaleString() }}
                  </template>
                </el-table-column>
                <el-table-column prop="duration" label="" width="100">
                  <template #default="scope">
                    {{ scope.row.duration }}
                  </template>
                </el-table-column>
                <el-table-column prop="risk_level" label="" width="100">
                  <template #default="scope">
                    <el-tag :type="getRiskTagType(scope.row.risk_level)">
                      {{ scope.row.risk_level }}
                    </el-tag>
                  </template>
                </el-table-column>
                <el-table-column label="" width="120">
                  <template #default="scope">
                    <el-button type="primary" size="small" @click="viewInsuranceDetail(scope.row)">
                      
                    </el-button>
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>
          </el-tabs>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import * as echarts from 'echarts'

export default {
  name: 'Dashboard',
  data() {
    return {
      userName: localStorage.getItem('userName') || '',
      userAvatar: '',
      totalAssets: 156800,
      todayProfit: 2345,
      recommendedProducts: 12,
      activeTab: 'funds',
      
      recommendedFunds: [],
      recommendedStocks: [],
      recommendedInsurance: [],

      assetChart: null,
      profitChart: null
    }
  },
  computed: {
    riskLevelText() {
      const risk = localStorage.getItem('userRisk')
      const riskMap = {
        'low': '',
        'medium': '', 
        'high': ''
      }
      return riskMap[risk] || ''
    }
  },
  async mounted() {
    await this.loadRecommendations()
    this.initCharts()
  },
  beforeUnmount() {
    if (this.assetChart) {
      this.assetChart.dispose()
    }
    if (this.profitChart) {
      this.profitChart.dispose()
    }
  },
  methods: {
    async loadRecommendations() {
      const userId = localStorage.getItem('userId')
      if (!userId) return

      try {
        // API
        this.recommendedFunds = [
          {
            id: 1,
            name: '',
            code: '000001',
            type: '',
            risk_level: 'medium',
            expected_return: 0.086
          },
          {
            id: 2,
            name: '',
            code: '110022',
            type: '',
            risk_level: 'high',
            expected_return: 0.124
          }
        ]

        this.recommendedStocks = [
          {
            id: 1,
            name: '',
            code: '600519',
            industry: '',
            pe_ratio: 35.2,
            dividend_yield: 0.012
          },
          {
            id: 2,
            name: '',
            code: '300750',
            industry: '',
            pe_ratio: 42.8,
            dividend_yield: 0.008
          }
        ]

        this.recommendedInsurance = [
          {
            id: 1,
            name: '',
            type: '',
            premium: 5000,
            duration: 10,
            risk_level: 'low'
          },
          {
            id: 2,
            name: '',
            type: '',
            premium: 8000,
            duration: 20,
            risk_level: 'medium'
          }
        ]
      } catch (error) {
        console.error(':', error)
      }
    },

    initCharts() {
      // 
      this.assetChart = echarts.init(this.$refs.assetChart)
      const assetOption = {
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'vertical',
          left: 'left'
        },
        series: [
          {
            name: '',
            type: 'pie',
            radius: '50%',
            data: [
              { value: 35, name: '' },
              { value: 25, name: '' },
              { value: 20, name: '' },
              { value: 15, name: '' },
              { value: 5, name: '' }
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      }
      this.assetChart.setOption(assetOption)

      // 
      this.profitChart = echarts.init(this.$refs.profitChart)
      const profitOption = {
        tooltip: {
          trigger: 'axis'
        },
        xAxis: {
          type: 'category',
          data: ['1', '2', '3', '4', '5', '6', '7']
        },
        yAxis: {
          type: 'value'
        },
        series: [
          {
            name: '',
            type: 'line',
            data: [1200, 1800, 1500, 2200, 1900, 2300, 2500],
            smooth: true,
            lineStyle: {
              color: '#409EFF'
            },
            areaStyle: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                { offset: 0, color: 'rgba(64, 158, 255, 0.3)' },
                { offset: 1, color: 'rgba(64, 158, 255, 0.1)' }
              ])
            }
          }
        ]
      }
      this.profitChart.setOption(profitOption)
    },

    getRiskTagType(riskLevel) {
      const typeMap = {
        'low': 'success',
        'medium': 'warning',
        'high': 'danger'
      }
      return typeMap[riskLevel] || 'info'
    },

    viewFundDetail(fund) {
      this.$router.push(`/funds?fundId=${fund.id}`)
    },

    viewStockDetail(stock) {
      this.$router.push(`/stocks?stockId=${stock.id}`)
    },

    viewInsuranceDetail(insurance) {
      this.$router.push(`/insurance?insuranceId=${insurance.id}`)
    }
  }
}
</script>

<style scoped>
.dashboard {
  padding: 20px;
}

.user-card {
  text-align: center;
}

.user-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.user-details h3 {
  margin: 0;
  color: #333;
}

.user-details p {
  margin: 5px 0 0 0;
  color: #666;
  font-size: 14px;
}

.stat-card {
  text-align: center;
}

.stat-card h2 {
  margin: 0;
  font-size: 24px;
}

.stat-card p {
  margin: 5px 0 0 0;
  color: #999;
  font-size: 12px;
}
</style>
