<template>
  <div class="stocks">
    <div class="card">
      <div class="card-header">
        <div class="header-content">
          <span class="title"></span>
          <div class="filter-section">
            <select v-model="filterIndustry" class="select">
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
            </select>
            
            <select v-model="filterTrend" class="select">
              <option value=""></option>
              <option value="up"></option>
              <option value="down"></option>
              <option value="sideways"></option>
            </select>
            
            <button class="btn btn-primary" @click="loadStocks"></button>
          </div>
        </div>
      </div>

      <div class="table-container">
        <table class="table" v-if="!loading">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="stock in paginatedStocks" :key="stock.id">
              <td>{{ stock.name }}</td>
              <td>{{ stock.code }}</td>
              <td>{{ stock.industry }}</td>
              <td>{{ stock.current_price.toFixed(2) }}</td>
              <td>
                <span :style="{ color: stock.change_percent >= 0 ? '#F56C6C' : '#67C23A' }">
                  {{ stock.change_percent >= 0 ? '+' : '' }}{{ stock.change_percent.toFixed(2) }}%
                </span>
              </td>
              <td>{{ stock.pe_ratio.toFixed(2) }}</td>
              <td>{{ formatMarketCap(stock.market_cap) }}</td>
              <td>
                <span :class="['tag', getTrendTagClass(stock.trend)]">
                  {{ getTrendText(stock.trend) }}
                </span>
              </td>
              <td>
                <div class="rating">
                  <span class="stars">
                    <span v-for="n in 5" :key="n" class="star" :class="{ active: n <= Math.floor(stock.recommendation_score) }">
                      
                    </span>
                  </span>
                  <span class="score">{{ stock.recommendation_score }}</span>
                </div>
              </td>
              <td>
                <button class="btn btn-sm btn-primary" @click="viewStockDetail(stock)">
                  
                </button>
                <button class="btn btn-sm btn-success" @click="simulateTrade(stock)">
                  
                </button>
              </td>
            </tr>
          </tbody>
        </table>
        <div v-if="loading" class="loading">...</div>
      </div>

      <!--  -->
      <div class="pagination-container">
        <div class="pagination-info">
           {{ totalStocks }} 
        </div>
        <div class="pagination-controls">
          <select v-model="pageSize" @change="handleSizeChange" class="select-sm">
            <option value="10">10/</option>
            <option value="20">20/</option>
            <option value="50">50/</option>
            <option value="100">100/</option>
          </select>
          
          <button class="btn btn-sm" :disabled="currentPage === 1" @click="currentPage--">
            
          </button>
          
          <span class="page-info">
             {{ currentPage }}  /  {{ totalPages }} 
          </span>
          
          <button class="btn btn-sm" :disabled="currentPage === totalPages" @click="currentPage++">
            
          </button>
        </div>
      </div>
    </div>

    <!--  -->
    <div v-if="detailDialogVisible" class="modal-overlay" @click="detailDialogVisible = false">
      <div class="modal" @click.stop>
        <div class="modal-header">
          <h3></h3>
          <button class="close-btn" @click="detailDialogVisible = false"></button>
        </div>
        <div class="modal-body" v-if="selectedStock">
          <div class="detail-grid">
            <div class="detail-item">
              <label>:</label>
              <span>{{ selectedStock.name }}</span>
            </div>
            <div class="detail-item">
              <label>:</label>
              <span>{{ selectedStock.code }}</span>
            </div>
            <div class="detail-item">
              <label>:</label>
              <span>{{ selectedStock.industry }}</span>
            </div>
            <div class="detail-item">
              <label>:</label>
              <span>{{ selectedStock.current_price.toFixed(2) }}</span>
            </div>
            <div class="detail-item">
              <label>:</label>
              <span :style="{ color: selectedStock.change_percent >= 0 ? '#F56C6C' : '#67C23A' }">
                {{ selectedStock.change_percent >= 0 ? '+' : '' }}{{ selectedStock.change_percent.toFixed(2) }}%
              </span>
            </div>
            <div class="detail-item">
              <label>:</label>
              <span>{{ selectedStock.pe_ratio.toFixed(2) }}</span>
            </div>
            <div class="detail-item">
              <label>:</label>
              <span>{{ formatMarketCap(selectedStock.market_cap) }}</span>
            </div>
            <div class="detail-item">
              <label>52:</label>
              <span>{{ selectedStock.high_52w.toFixed(2) }}</span>
            </div>
            <div class="detail-item">
              <label>52:</label>
              <span>{{ selectedStock.low_52w.toFixed(2) }}</span>
            </div>
            <div class="detail-item">
              <label>:</label>
              <span :class="['tag', getTrendTagClass(selectedStock.trend)]">
                {{ getTrendText(selectedStock.trend) }}
              </span>
            </div>
            <div class="detail-item full-width">
              <label>:</label>
              <span>{{ selectedStock.description }}</span>
            </div>
          </div>

          <!--  -->
          <div class="similar-section">
            <h4></h4>
            <table class="table table-sm">
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="stock in similarStocks" :key="stock.id">
                  <td>{{ stock.name }}</td>
                  <td>{{ stock.code }}</td>
                  <td>{{ stock.current_price.toFixed(2) }}</td>
                  <td>
                    <span :style="{ color: stock.change_percent >= 0 ? '#F56C6C' : '#67C23A' }">
                      {{ stock.change_percent >= 0 ? '+' : '' }}{{ stock.change_percent.toFixed(2) }}%
                    </span>
                  </td>
                  <td>
                    <button class="btn btn-sm btn-text" @click="viewStockDetail(stock)"></button>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <div v-if="tradeDialogVisible" class="modal-overlay" @click="tradeDialogVisible = false">
      <div class="modal" @click.stop>
        <div class="modal-header">
          <h3></h3>
          <button class="close-btn" @click="tradeDialogVisible = false"></button>
        </div>
        <div class="modal-body" v-if="selectedStock">
          <div class="form-group">
            <label>:</label>
            <div class="radio-group">
              <label class="radio-label">
                <input type="radio" v-model="tradeForm.type" value="buy">
                <span></span>
              </label>
              <label class="radio-label">
                <input type="radio" v-model="tradeForm.type" value="sell">
                <span></span>
              </label>
            </div>
          </div>
          <div class="form-group">
            <label>:</label>
            <input 
              type="number" 
              v-model.number="tradeForm.quantity" 
              :min="100"
              :step="100"
              class="input"
            />
            <div class="help-text">
              : 100
            </div>
          </div>
          <div class="form-group">
            <label>:</label>
            <span>{{ selectedStock.current_price.toFixed(2) }}</span>
          </div>
          <div class="form-group">
            <label>:</label>
            <span class="highlight-primary">
              {{ (tradeForm.quantity * selectedStock.current_price).toFixed(2) }}
            </span>
          </div>
          <div class="modal-footer">
            <button class="btn" @click="tradeDialogVisible = false"></button>
            <button class="btn btn-primary" @click="confirmTrade"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Stocks',
  data() {
    return {
      stocks: [],
      filteredStocks: [],
      loading: false,
      currentPage: 1,
      pageSize: 10,
      totalStocks: 0,
      
      filterIndustry: '',
      filterTrend: '',
      
      detailDialogVisible: false,
      selectedStock: null,
      similarStocks: [],
      
      tradeDialogVisible: false,
      tradeForm: {
        type: 'buy',
        quantity: 100
      }
    }
  },
  computed: {
    totalPages() {
      return Math.ceil(this.totalStocks / this.pageSize)
    },
    paginatedStocks() {
      const start = (this.currentPage - 1) * this.pageSize
      const end = start + this.pageSize
      return this.filteredStocks.slice(start, end)
    }
  },
  async mounted() {
    await this.loadStocks()
  },
  methods: {
    async loadStocks() {
      this.loading = true
      try {
        // API
        this.stocks = [
          {
            id: 1,
            name: '',
            code: '00700',
            industry: '',
            current_price: 320.50,
            change_percent: 2.15,
            pe_ratio: 25.8,
            market_cap: 3800000000000,
            high_52w: 450.00,
            low_52w: 280.00,
            trend: 'up',
            description: '',
            recommendation_score: 4.3
          },
          {
            id: 2,
            name: '',
            code: '600519',
            industry: '',
            current_price: 1680.00,
            change_percent: -1.25,
            pe_ratio: 35.2,
            market_cap: 2100000000000,
            high_52w: 2000.00,
            low_52w: 1500.00,
            trend: 'sideways',
            description: '',
            recommendation_score: 4.1
          },
          {
            id: 3,
            name: '',
            code: '600036',
            industry: '',
            current_price: 32.80,
            change_percent: 0.92,
            pe_ratio: 8.5,
            market_cap: 850000000000,
            high_52w: 40.00,
            low_52w: 28.00,
            trend: 'up',
            description: '',
            recommendation_score: 4.0
          },
          {
            id: 4,
            name: '',
            code: '300750',
            industry: '',
            current_price: 185.60,
            change_percent: 3.45,
            pe_ratio: 42.3,
            market_cap: 820000000000,
            high_52w: 220.00,
            low_52w: 160.00,
            trend: 'up',
            description: '',
            recommendation_score: 4.5
          },
          {
            id: 5,
            name: '',
            code: '600276',
            industry: '',
            current_price: 45.20,
            change_percent: -0.88,
            pe_ratio: 38.7,
            market_cap: 290000000000,
            high_52w: 60.00,
            low_52w: 40.00,
            trend: 'down',
            description: '',
            recommendation_score: 3.8
          }
        ]

        // 
        this.applyFilters()
        this.totalStocks = this.filteredStocks.length
        
      } catch (error) {
        console.error(':', error)
      } finally {
        this.loading = false
      }
    },

    applyFilters() {
      let filtered = [...this.stocks]
      
      if (this.filterIndustry) {
        filtered = filtered.filter(stock => stock.industry === this.filterIndustry)
      }
      
      if (this.filterTrend) {
        filtered = filtered.filter(stock => stock.trend === this.filterTrend)
      }
      
      this.filteredStocks = filtered
    },

    formatMarketCap(cap) {
      if (cap >= 1000000000000) {
        return (cap / 1000000000000).toFixed(2) + ''
      } else if (cap >= 100000000) {
        return (cap / 100000000).toFixed(2) + ''
      } else {
        return cap.toLocaleString()
      }
    },

    getTrendTagClass(trend) {
      const classMap = {
        'up': 'tag-success',
        'down': 'tag-danger',
        'sideways': 'tag-warning'
      }
      return classMap[trend] || 'tag-info'
    },

    getTrendText(trend) {
      const textMap = {
        'up': '',
        'down': '',
        'sideways': ''
      }
      return textMap[trend] || ''
    },

    async viewStockDetail(stock) {
      this.selectedStock = stock
      this.detailDialogVisible = true
      
      // 
      this.similarStocks = this.stocks
        .filter(s => s.id !== stock.id && s.industry === stock.industry)
        .slice(0, 3)
    },

    simulateTrade(stock) {
      this.selectedStock = stock
      this.tradeForm.quantity = 100
      this.tradeDialogVisible = true
    },

    confirmTrade() {
      const action = this.tradeForm.type === 'buy' ? '' : ''
      const totalAmount = this.tradeForm.quantity * this.selectedStock.current_price
      alert(`${action} ${this.tradeForm.quantity}${this.selectedStock.name} ${totalAmount.toFixed(2)}`)
      this.tradeDialogVisible = false
    },

    handleSizeChange() {
      this.currentPage = 1
    }
  }
}
</script>

<style scoped>
.stocks {
  padding: 20px;
}

.card {
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.card-header {
  padding: 20px;
  border-bottom: 1px solid #ebeef5;
  background: #f5f7fa;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.title {
  font-size: 18px;
  font-weight: 600;
  color: #303133;
}

.filter-section {
  display: flex;
  align-items: center;
  gap: 10px;
}

.select {
  padding: 8px 12px;
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  background: white;
  min-width: 120px;
}

.select-sm {
  padding: 4px 8px;
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  background: white;
}

.btn {
  padding: 8px 16px;
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  background: white;
  cursor: pointer;
  transition: all 0.3s;
}

.btn:hover {
  background: #f5f7fa;
}

.btn-primary {
  background: #409eff;
  color: white;
  border-color: #409eff;
}

.btn-primary:hover {
  background: #66b1ff;
}

.btn-success {
  background: #67c23a;
  color: white;
  border-color: #67c23a;
}

.btn-success:hover {
  background: #85ce61;
}

.btn-sm {
  padding: 4px 8px;
  font-size: 12px;
}

.btn-text {
  background: transparent;
  border: none;
  color: #409eff;
}

.btn-text:hover {
  background: transparent;
  color: #66b1ff;
}

.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.table-container {
  padding: 20px;
}

.table {
  width: 100%;
  border-collapse: collapse;
}

.table th,
.table td {
  padding: 12px;
  text-align: left;
  border-bottom: 1px solid #ebeef5;
}

.table th {
  background: #f5f7fa;
  font-weight: 600;
  color: #606266;
}

.table-sm th,
.table-sm td {
  padding: 8px;
  font-size: 12px;
}

.tag {
  display: inline-block;
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 12px;
  color: white;
}

.tag-success {
  background: #67c23a;
}

.tag-danger {
  background: #f56c6c;
}

.tag-warning {
  background: #e6a23c;
}

.tag-info {
  background: #909399;
}

.rating {
  display: flex;
  align-items: center;
  gap: 8px;
}

.stars {
  color: #c0c4cc;
}

.star.active {
  color: #f7ba2a;
}

.score {
  font-size: 12px;
  color: #909399;
}

.pagination-container {
  padding: 20px;
  border-top: 1px solid #ebeef5;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.pagination-controls {
  display: flex;
  align-items: center;
  gap: 10px;
}

.page-info {
  font-size: 14px;
  color: #606266;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal {
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  min-width: 500px;
  max-width: 80vw;
  max-height: 80vh;
  overflow: auto;
}

.modal-header {
  padding: 20px;
  border-bottom: 1px solid #ebeef5;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-header h3 {
  margin: 0;
  font-size: 18px;
  color: #303133;
}

.close-btn {
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
  color: #909399;
}

.close-btn:hover {
  color: #606266;
}

.modal-body {
  padding: 20px;
}

.modal-footer {
  padding: 20px;
  border-top: 1px solid #ebeef5;
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.detail-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
}

.detail-item {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.detail-item.full-width {
  grid-column: 1 / -1;
}

.detail-item label {
  font-weight: 600;
  color: #606266;
  font-size: 14px;
}

.similar-section {
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid #ebeef5;
}

.similar-section h4 {
  margin: 0 0 16px 0;
  font-size: 16px;
  color: #303133;
}

.form-group {
  margin-bottom: 16px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 600;
  color: #606266;
}

.radio-group {
  display: flex;
  gap: 20px;
}

.radio-label {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}

.input {
  width: 100%;
  padding: 8px 12px;
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  box-sizing: border-box;
}

.help-text {
  font-size: 12px;
  color: #909399;
  margin-top: 4px;
}

.highlight-primary {
  color: #409eff;
  font-weight: bold;
}

.loading {
  text-align: center;
  padding: 40px;
  color: #909399;
}
</style>
