<template>
  <div class="financial-diagnosis">
    <el-card>
      <template #header>
        <div class="header-content">
          <span></span>
          <el-button type="primary" @click="startDiagnosis" :disabled="diagnosisStarted">
            {{ diagnosisStarted ? '...' : '' }}
          </el-button>
        </div>
      </template>

      <!--  -->
      <div v-if="diagnosisStarted" class="diagnosis-steps">
        <el-steps :active="currentStep" align-center>
          <el-step title="" description=""></el-step>
          <el-step title="" description=""></el-step>
          <el-step title="" description=""></el-step>
          <el-step title="" description=""></el-step>
          <el-step title="" description=""></el-step>
        </el-steps>

        <!--  -->
        <div class="step-content">
          <!-- 1:  -->
          <div v-if="currentStep === 1" class="step-form">
            <h3></h3>
            <el-form :model="basicInfo" label-width="100px">
              <el-form-item label="">
                <el-input-number
                  v-model="basicInfo.age"
                  :min="18"
                  :max="80"
                  style="width: 200px"
                />
              </el-form-item>
              <el-form-item label="">
                <el-select v-model="basicInfo.occupation" placeholder="" style="width: 200px">
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-input-number
                  v-model="basicInfo.income"
                  :min="0"
                  :step="10000"
                  style="width: 200px"
                />
                <span style="margin-left: 10px"></span>
              </el-form-item>
              <el-form-item label="">
                <el-radio-group v-model="basicInfo.marital_status">
                  <el-radio label=""></el-radio>
                  <el-radio label=""></el-radio>
                  <el-radio label=""></el-radio>
                </el-radio-group>
              </el-form-item>
              <el-form-item label="">
                <el-input-number
                  v-model="basicInfo.family_members"
                  :min="1"
                  :max="10"
                  style="width: 200px"
                />
              </el-form-item>
            </el-form>
          </div>

          <!-- 2:  -->
          <div v-if="currentStep === 2" class="step-form">
            <h3></h3>
            <el-form :model="financialStatus" label-width="120px">
              <el-form-item label="">
                <el-input-number
                  v-model="financialStatus.monthly_expenses"
                  :min="0"
                  :step="1000"
                  style="width: 200px"
                />
                <span style="margin-left: 10px"></span>
              </el-form-item>
              <el-form-item label="">
                <el-input-number
                  v-model="financialStatus.savings"
                  :min="0"
                  :step="10000"
                  style="width: 200px"
                />
                <span style="margin-left: 10px"></span>
              </el-form-item>
              <el-form-item label="">
                <el-input-number
                  v-model="financialStatus.debt"
                  :min="0"
                  :step="10000"
                  style="width: 200px"
                />
                <span style="margin-left: 10px"></span>
              </el-form-item>
              <el-form-item label="">
                <el-radio-group v-model="financialStatus.investment_experience">
                  <el-radio label=""></el-radio>
                  <el-radio label="1-3">1-3</el-radio>
                  <el-radio label="3-5">3-5</el-radio>
                  <el-radio label="5">5</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-form>
          </div>

          <!-- 3:  -->
          <div v-if="currentStep === 3" class="step-form">
            <h3></h3>
            <div class="risk-questions">
              <div v-for="(question, index) in riskQuestions" :key="index" class="question-item">
                <p>{{ question.text }}</p>
                <el-radio-group v-model="riskAnswers[index]">
                  <el-radio 
                    v-for="(option, optIndex) in question.options" 
                    :key="optIndex" 
                    :label="option.score"
                    style="display: block; margin: 10px 0;"
                  >
                    {{ option.text }}
                  </el-radio>
                </el-radio-group>
              </div>
            </div>
          </div>

          <!-- 4:  -->
          <div v-if="currentStep === 4" class="step-form">
            <h3></h3>
            <el-form :model="financialGoals" label-width="120px">
              <el-form-item label="(1)">
                <el-input
                  v-model="financialGoals.short_term"
                  type="textarea"
                  :rows="3"
                  placeholder=""
                  style="width: 400px"
                />
              </el-form-item>
              <el-form-item label="(1-5)">
                <el-input
                  v-model="financialGoals.mid_term"
                  type="textarea"
                  :rows="3"
                  placeholder=""
                  style="width: 400px"
                />
              </el-form-item>
              <el-form-item label="(5)">
                <el-input
                  v-model="financialGoals.long_term"
                  type="textarea"
                  :rows="3"
                  placeholder=""
                  style="width: 400px"
                />
              </el-form-item>
              <el-form-item label="">
                <el-slider
                  v-model="financialGoals.expected_return"
                  :min="3"
                  :max="20"
                  :step="1"
                  show-stops
                  style="width: 400px"
                />
                <span style="margin-left: 20px">{{ financialGoals.expected_return }}%</span>
              </el-form-item>
            </el-form>
          </div>

          <!-- 5:  -->
          <div v-if="currentStep === 5" class="diagnosis-result">
            <h3></h3>
            <el-row :gutter="20">
              <el-col :span="8">
                <el-card>
                  <template #header>
                    <span></span>
                  </template>
                  <div class="risk-level">
                    <div class="risk-icon" :class="diagnosisResult.riskLevel">
                      <i class="el-icon-warning"></i>
                    </div>
                    <h4>{{ diagnosisResult.riskLevelText }}</h4>
                    <p>{{ diagnosisResult.riskDescription }}</p>
                  </div>
                </el-card>
              </el-col>
              <el-col :span="8">
                <el-card>
                  <template #header>
                    <span></span>
                  </template>
                  <div class="allocation-chart">
                    <div 
                      v-for="(item, index) in diagnosisResult.assetAllocation" 
                      :key="index"
                      class="allocation-item"
                      :style="{ backgroundColor: item.color }"
                    >
                      <span>{{ item.name }}: {{ item.percentage }}%</span>
                    </div>
                  </div>
                </el-card>
              </el-col>
              <el-col :span="8">
                <el-card>
                  <template #header>
                    <span></span>
                  </template>
                  <div class="health-score">
                    <el-progress
                      type="circle"
                      :percentage="diagnosisResult.healthScore"
                      :color="diagnosisResult.healthColor"
                    />
                    <p>{{ diagnosisResult.healthText }}</p>
                  </div>
                </el-card>
              </el-col>
            </el-row>

            <!--  -->
            <el-card style="margin-top: 20px;">
              <template #header>
                <span></span>
              </template>
              <div class="recommendations">
                <div v-for="(recommendation, index) in diagnosisResult.recommendations" :key="index" class="recommendation-item">
                  <h4>{{ recommendation.title }}</h4>
                  <p>{{ recommendation.content }}</p>
                </div>
              </div>
            </el-card>

            <!--  -->
            <el-card style="margin-top: 20px;">
              <template #header>
                <span></span>
              </template>
              <el-table :data="diagnosisResult.recommendedProducts" style="width: 100%">
                <el-table-column prop="type" label="" width="100"></el-table-column>
                <el-table-column prop="name" label="" width="200"></el-table-column>
                <el-table-column prop="description" label=""></el-table-column>
                <el-table-column prop="suitability" label="" width="100">
                  <template #default="scope">
                    <el-tag :type="scope.row.suitability > 80 ? 'success' : scope.row.suitability > 60 ? 'warning' : 'info'">
                      {{ scope.row.suitability }}%
                    </el-tag>
                  </template>
                </el-table-column>
              </el-table>
            </el-card>
          </div>
        </div>

        <!--  -->
        <div class="step-actions" style="margin-top: 20px; text-align: center;">
          <el-button @click="prevStep" :disabled="currentStep === 1"></el-button>
          <el-button type="primary" @click="nextStep" v-if="currentStep < 5">
            {{ currentStep === 4 ? '' : '' }}
          </el-button>
          <el-button type="success" @click="saveDiagnosis" v-if="currentStep === 5">
            
          </el-button>
        </div>
      </div>

      <!--  -->
      <div v-else class="welcome-section">
        <div class="welcome-content">
          <h2></h2>
          <p></p>
          <div class="features">
            <el-row :gutter="20">
              <el-col :span="6">
                <div class="feature-item">
                  <i class="el-icon-user"></i>
                  <h4></h4>
                  <p></p>
                </div>
              </el-col>
              <el-col :span="6">
                <div class="feature-item">
                  <i class="el-icon-data-analysis"></i>
                  <h4></h4>
                  <p></p>
                </div>
              </el-col>
              <el-col :span="6">
                <div class="feature-item">
                  <i class="el-icon-present"></i>
                  <h4></h4>
                  <p></p>
                </div>
              </el-col>
              <el-col :span="6">
                <div class="feature-item">
                  <i class="el-icon-document"></i>
                  <h4></h4>
                  <p></p>
                </div>
              </el-col>
            </el-row>
          </div>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'FinancialDiagnosis',
  data() {
    return {
      diagnosisStarted: false,
      currentStep: 1,
      
      // 
      basicInfo: {
        age: 30,
        occupation: '',
        income: 0,
        marital_status: '',
        family_members: 1
      },
      
      // 
      financialStatus: {
        monthly_expenses: 0,
        savings: 0,
        debt: 0,
        investment_experience: ''
      },
      
      // 
      riskQuestions: [
        {
          text: '',
          options: [
            { text: '', score: 1 },
            { text: '10%', score: 2 },
            { text: '20%', score: 3 },
            { text: '30%', score: 4 }
          ]
        },
        {
          text: '',
          options: [
            { text: '1', score: 1 },
            { text: '1-3', score: 2 },
            { text: '3-5', score: 3 },
            { text: '5', score: 4 }
          ]
        },
        {
          text: '',
          options: [
            { text: '', score: 1 },
            { text: '', score: 2 },
            { text: '', score: 3 },
            { text: '', score: 4 }
          ]
        }
      ],
      riskAnswers: [1, 1, 1],
      
      // 
      financialGoals: {
        short_term: '',
        mid_term: '',
        long_term: '',
        expected_return: 8
      },
      
      // 
      diagnosisResult: {
        riskLevel: 'conservative',
        riskLevelText: '',
        riskDescription: '',
        healthScore: 65,
        healthColor: '#E6A23C',
        healthText: '',
        assetAllocation: [
          { name: '', percentage: 40, color: '#409EFF' },
          { name: '', percentage: 30, color: '#67C23A' },
          { name: '', percentage: 20, color: '#E6A23C' },
          { name: '', percentage: 10, color: '#F56C6C' }
        ],
        recommendations: [
          {
            title: '',
            content: '3-6'
          },
          {
            title: '',
            content: ''
          },
          {
            title: '',
            content: ''
          }
        ],
        recommendedProducts: [
          {
            type: '',
            name: '',
            description: '',
            suitability: 85
          },
          {
            type: '',
            name: '',
            description: '',
            suitability: 78
          },
          {
            type: '',
            name: '',
            description: '',
            suitability: 90
          }
        ]
      }
    }
  },
  methods: {
    startDiagnosis() {
      this.diagnosisStarted = true
      this.currentStep = 1
    },
    
    nextStep() {
      if (this.currentStep < 5) {
        this.currentStep++
        
        // 
        if (this.currentStep === 5) {
          this.calculateDiagnosisResult()
        }
      }
    },
    
    prevStep() {
      if (this.currentStep > 1) {
        this.currentStep--
      }
    },
    
    calculateDiagnosisResult() {
      // 
      const riskScore = this.riskAnswers.reduce((sum, score) => sum + score, 0)
      
      // 
      if (riskScore <= 3) {
        this.diagnosisResult.riskLevel = 'conservative'
        this.diagnosisResult.riskLevelText = ''
        this.diagnosisResult.riskDescription = ''
        this.diagnosisResult.assetAllocation = [
          { name: '', percentage: 40, color: '#409EFF' },
          { name: '', percentage: 30, color: '#67C23A' },
          { name: '', percentage: 20, color: '#E6A23C' },
          { name: '', percentage: 10, color: '#F56C6C' }
        ]
      } else if (riskScore <= 6) {
        this.diagnosisResult.riskLevel = 'moderate'
        this.diagnosisResult.riskLevelText = ''
        this.diagnosisResult.riskDescription = ''
        this.diagnosisResult.assetAllocation = [
          { name: '', percentage: 30, color: '#409EFF' },
          { name: '', percentage: 35, color: '#67C23A' },
          { name: '', percentage: 25, color: '#E6A23C' },
          { name: '', percentage: 10, color: '#F56C6C' }
        ]
      } else if (riskScore <= 9) {
        this.diagnosisResult.riskLevel = 'aggressive'
        this.diagnosisResult.riskLevelText = ''
        this.diagnosisResult.riskDescription = ''
        this.diagnosisResult.assetAllocation = [
          { name: '', percentage: 20, color: '#409EFF' },
          { name: '', percentage: 25, color: '#67C23A' },
          { name: '', percentage: 30, color: '#E6A23C' },
          { name: '', percentage: 25, color: '#F56C6C' }
        ]
      } else {
        this.diagnosisResult.riskLevel = 'very_aggressive'
        this.diagnosisResult.riskLevelText = ''
        this.diagnosisResult.riskDescription = ''
        this.diagnosisResult.assetAllocation = [
          { name: '', percentage: 10, color: '#409EFF' },
          { name: '', percentage: 20, color: '#67C23A' },
          { name: '', percentage: 30, color: '#E6A23C' },
          { name: '', percentage: 40, color: '#F56C6C' }
        ]
      }

      // 
      const savingsRatio = this.financialStatus.savings / this.basicInfo.income
      const debtRatio = this.financialStatus.debt / this.basicInfo.income
      
      let healthScore = 70
      if (savingsRatio > 0.5) healthScore += 15
      else if (savingsRatio > 0.3) healthScore += 10
      else if (savingsRatio > 0.1) healthScore += 5
      
      if (debtRatio < 0.3) healthScore += 10
      else if (debtRatio < 0.5) healthScore += 5
      else healthScore -= 10
      
      this.diagnosisResult.healthScore = Math.min(100, Math.max(0, healthScore))
      
      if (this.diagnosisResult.healthScore >= 80) {
        this.diagnosisResult.healthColor = '#67C23A'
        this.diagnosisResult.healthText = ''
      } else if (this.diagnosisResult.healthScore >= 60) {
        this.diagnosisResult.healthColor = '#E6A23C'
        this.diagnosisResult.healthText = ''
      } else {
        this.diagnosisResult.healthColor = '#F56C6C'
        this.diagnosisResult.healthText = ''
      }
    },
    
    saveDiagnosis() {
      this.$message.success('')
      this.diagnosisStarted = false
      this.currentStep = 1
    }
  }
}
</script>

<style scoped>
.financial-diagnosis {
  padding: 20px;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.diagnosis-steps {
  margin-bottom: 30px;
}

.step-form {
  padding: 20px;
  border: 1px solid #ebeef5;
  border-radius: 4px;
  margin: 20px 0;
}

.risk-questions .question-item {
  margin-bottom: 30px;
  padding: 15px;
  border: 1px solid #ebeef5;
  border-radius: 4px;
}

.diagnosis-result {
  padding: 20px 0;
}

.risk-level {
  text-align: center;
  padding: 20px;
}

.risk-icon {
  font-size: 48px;
  margin-bottom: 15px;
}

.risk-icon.conservative {
  color: #67C23A;
}

.risk-icon.moderate {
  color: #E6A23C;
}

.risk-icon.aggressive {
  color: #F56C6C;
}

.risk-icon.very_aggressive {
  color: #F56C6C;
}

.allocation-chart {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.allocation-item {
  padding: 8px 12px;
  border-radius: 4px;
  color: white;
  font-weight: bold;
  text-align: center;
}

.health-score {
  text-align: center;
  padding: 20px;
}

.recommendations {
  padding: 10px;
}

.recommendation-item {
  margin-bottom: 20px;
  padding-bottom: 15px;
  border-bottom: 1px solid #ebeef5;
}

.recommendation-item:last-child {
  border-bottom: none;
}

.welcome-section {
  text-align: center;
  padding: 40px 20px;
}

.welcome-content h2 {
  color: #303133;
  margin-bottom: 15px;
}

.welcome-content p {
  color: #606266;
  font-size: 16px;
  margin-bottom: 30px;
}

.features {
  margin-top: 40px;
}

.feature-item {
  text-align: center;
  padding: 20px;
}

.feature-item i {
  font-size: 48px;
  color: #409EFF;
  margin-bottom: 15px;
}

.feature-item h4 {
  color: #303133;
  margin-bottom: 10px;
}

.feature-item p {
  color: #606266;
  font-size: 14px;
}

.step-actions {
  margin-top: 30px;
  text-align: center;
}
</style>
