<template>
  <div class="insurance">
    <div class="card">
      <div class="card-header">
        <div class="header-content">
          <span class="title"></span>
          <div class="filter-section">
            <select v-model="filterType" class="select">
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
            </select>
            
            <select v-model="filterCoverage" class="select">
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
            </select>
            
            <button class="btn btn-primary" @click="loadInsurance"></button>
          </div>
        </div>
      </div>

      <div class="table-container">
        <table class="table" v-if="!loading">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="insurance in paginatedInsurance" :key="insurance.id">
              <td>{{ insurance.name }}</td>
              <td>{{ insurance.company }}</td>
              <td>{{ insurance.type }}</td>
              <td>
                <span :class="['tag', getCoverageTagClass(insurance.coverage)]">
                  {{ insurance.coverage }}
                </span>
              </td>
              <td>{{ insurance.premium.toLocaleString() }}</td>
              <td>{{ insurance.coverage_amount.toLocaleString() }}</td>
              <td>{{ insurance.term }}</td>
              <td>{{ insurance.age_range }}</td>
              <td>
                <div class="rating">
                  <span class="stars">
                    <span v-for="n in 5" :key="n" class="star" :class="{ active: n <= Math.floor(insurance.recommendation_score) }">
                      
                    </span>
                  </span>
                  <span class="score">{{ insurance.recommendation_score }}</span>
                </div>
              </td>
              <td>
                <button class="btn btn-sm btn-primary" @click="viewInsuranceDetail(insurance)">
                  
                </button>
                <button class="btn btn-sm btn-success" @click="simulatePurchase(insurance)">
                  
                </button>
              </td>
            </tr>
          </tbody>
        </table>
        <div v-if="loading" class="loading">...</div>
      </div>

      <!--  -->
      <div class="pagination-container">
        <div class="pagination-info">
           {{ totalInsurance }} 
        </div>
        <div class="pagination-controls">
          <select v-model="pageSize" @change="handleSizeChange" class="select-sm">
            <option value="10">10/</option>
            <option value="20">20/</option>
            <option value="50">50/</option>
            <option value="100">100/</option>
          </select>
          
          <button class="btn btn-sm" :disabled="currentPage === 1" @click="currentPage--">
            
          </button>
          
          <span class="page-info">
             {{ currentPage }}  /  {{ totalPages }} 
          </span>
          
          <button class="btn btn-sm" :disabled="currentPage === totalPages" @click="currentPage++">
            
          </button>
        </div>
      </div>
    </div>

    <!--  -->
    <div v-if="detailDialogVisible" class="modal-overlay" @click="detailDialogVisible = false">
      <div class="modal" @click.stop>
        <div class="modal-header">
          <h3></h3>
          <button class="close-btn" @click="detailDialogVisible = false"></button>
        </div>
        <div class="modal-body" v-if="selectedInsurance">
          <div class="detail-grid">
            <div class="detail-item">
              <label>:</label>
              <span>{{ selectedInsurance.name }}</span>
            </div>
            <div class="detail-item">
              <label>:</label>
              <span>{{ selectedInsurance.company }}</span>
            </div>
            <div class="detail-item">
              <label>:</label>
              <span>{{ selectedInsurance.type }}</span>
            </div>
            <div class="detail-item">
              <label>:</label>
              <span :class="['tag', getCoverageTagClass(selectedInsurance.coverage)]">
                {{ selectedInsurance.coverage }}
              </span>
            </div>
            <div class="detail-item">
              <label>:</label>
              <span>{{ selectedInsurance.premium.toLocaleString() }}</span>
            </div>
            <div class="detail-item">
              <label>:</label>
              <span>{{ selectedInsurance.coverage_amount.toLocaleString() }}</span>
            </div>
            <div class="detail-item">
              <label>:</label>
              <span>{{ selectedInsurance.term }}</span>
            </div>
            <div class="detail-item">
              <label>:</label>
              <span>{{ selectedInsurance.age_range }}</span>
            </div>
            <div class="detail-item">
              <label>:</label>
              <span>{{ selectedInsurance.waiting_period }}</span>
            </div>
            <div class="detail-item full-width">
              <label>:</label>
              <span>{{ selectedInsurance.description }}</span>
            </div>
            <div class="detail-item full-width">
              <label>:</label>
              <div class="coverage-list">
                <div v-for="(item, index) in selectedInsurance.coverage_details" :key="index" class="coverage-item">
                   {{ item }}
                </div>
              </div>
            </div>
          </div>

          <!--  -->
          <div class="similar-section">
            <h4></h4>
            <table class="table table-sm">
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="insurance in similarInsurance" :key="insurance.id">
                  <td>{{ insurance.name }}</td>
                  <td>{{ insurance.company }}</td>
                  <td>{{ insurance.premium.toLocaleString() }}</td>
                  <td>
                    <button class="btn btn-sm btn-text" @click="viewInsuranceDetail(insurance)"></button>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <div v-if="purchaseDialogVisible" class="modal-overlay" @click="purchaseDialogVisible = false">
      <div class="modal" @click.stop>
        <div class="modal-header">
          <h3></h3>
          <button class="close-btn" @click="purchaseDialogVisible = false"></button>
        </div>
        <div class="modal-body" v-if="selectedInsurance">
          <div class="form-group">
            <label>:</label>
            <input 
              type="text" 
              v-model="purchaseForm.insured_name" 
              placeholder=""
              class="input"
            />
          </div>
          <div class="form-group">
            <label>:</label>
            <input 
              type="number" 
              v-model.number="purchaseForm.insured_age" 
              :min="18"
              :max="65"
              class="input"
            />
          </div>
          <div class="form-group">
            <label>:</label>
            <span>{{ selectedInsurance.term }}</span>
          </div>
          <div class="form-group">
            <label>:</label>
            <span class="highlight-primary">
              {{ selectedInsurance.premium.toLocaleString() }}
            </span>
          </div>
          <div class="form-group">
            <label>:</label>
            <span class="highlight-success">
              {{ (selectedInsurance.premium * selectedInsurance.term).toLocaleString() }}
            </span>
          </div>
          <div class="modal-footer">
            <button class="btn" @click="purchaseDialogVisible = false"></button>
            <button class="btn btn-primary" @click="confirmPurchase"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Insurance',
  data() {
    return {
      insurance: [],
      filteredInsurance: [],
      loading: false,
      currentPage: 1,
      pageSize: 10,
      totalInsurance: 0,
      
      filterType: '',
      filterCoverage: '',
      
      detailDialogVisible: false,
      selectedInsurance: null,
      similarInsurance: [],
      
      purchaseDialogVisible: false,
      purchaseForm: {
        insured_name: '',
        insured_age: 30
      }
    }
  },
  computed: {
    totalPages() {
      return Math.ceil(this.totalInsurance / this.pageSize)
    },
    paginatedInsurance() {
      const start = (this.currentPage - 1) * this.pageSize
      const end = start + this.pageSize
      return this.filteredInsurance.slice(start, end)
    }
  },
  mounted() {
    this.loadInsurance()
  },
  methods: {
    loadInsurance() {
      this.loading = true
      try {
        // 
        this.insurance = [
          {
            id: 1,
            name: '',
            company: '',
            type: '',
            coverage: '',
            premium: 5000,
            coverage_amount: 1000000,
            term: 30,
            age_range: '18-60',
            waiting_period: 90,
            description: '',
            coverage_details: [
              '100',
              '100',
              '',
              ''
            ],
            recommendation_score: 4.2
          },
          {
            id: 2,
            name: '',
            company: '',
            type: '',
            coverage: '',
            premium: 8000,
            coverage_amount: 500000,
            term: 20,
            age_range: '18-55',
            waiting_period: 180,
            description: '120',
            coverage_details: [
              '120',
              '',
              '',
              ''
            ],
            recommendation_score: 4.5
          },
          {
            id: 3,
            name: '',
            company: '',
            type: '',
            coverage: '',
            premium: 300,
            coverage_amount: 200000,
            term: 1,
            age_range: '18-65',
            waiting_period: 0,
            description: '',
            coverage_details: [
              '20',
              '20',
              '2',
              '100/'
            ],
            recommendation_score: 4.0
          },
          {
            id: 4,
            name: '',
            company: '',
            type: '',
            coverage: '',
            premium: 800,
            coverage_amount: 500000,
            term: 1,
            age_range: '',
            waiting_period: 0,
            description: '',
            coverage_details: [
              '50',
              '10',
              '5',
              ''
            ],
            recommendation_score: 3.8
          },
          {
            id: 5,
            name: '',
            company: '',
            type: '',
            coverage: '',
            premium: 20000,
            coverage_amount: 1000000,
            term: 20,
            age_range: '30-50',
            waiting_period: 0,
            description: '',
            coverage_details: [
              '60',
              '20',
              '',
              ''
            ],
            recommendation_score: 4.3
          }
        ]

        // 
        this.applyFilters()
        this.totalInsurance = this.filteredInsurance.length
        
      } catch (error) {
        console.error(':', error)
      } finally {
        this.loading = false
      }
    },

    applyFilters() {
      let filtered = [...this.insurance]
      
      if (this.filterType) {
        filtered = filtered.filter(item => item.type === this.filterType)
      }
      
      if (this.filterCoverage) {
        filtered = filtered.filter(item => item.coverage === this.filterCoverage)
      }
      
      this.filteredInsurance = filtered
    },

    getCoverageTagClass(coverage) {
      const classMap = {
        '': 'tag-info',
        '': 'tag-warning',
        '': 'tag-success'
      }
      return classMap[coverage] || 'tag-info'
    },

    viewInsuranceDetail(insurance) {
      this.selectedInsurance = insurance
      this.detailDialogVisible = true
      
      // 
      this.similarInsurance = this.insurance
        .filter(item => item.id !== insurance.id && item.type === insurance.type)
        .slice(0, 3)
    },

    simulatePurchase(insurance) {
      this.selectedInsurance = insurance
      this.purchaseForm.insured_name = ''
      this.purchaseForm.insured_age = 30
      this.purchaseDialogVisible = true
    },

    confirmPurchase() {
      if (!this.purchaseForm.insured_name) {
        alert('')
        return
      }
      
      const totalPremium = this.selectedInsurance.premium * this.selectedInsurance.term
      alert(`${this.purchaseForm.insured_name}${this.selectedInsurance.name} ${totalPremium.toLocaleString()}`)
      this.purchaseDialogVisible = false
    },

    handleSizeChange() {
      this.currentPage = 1
    }
  }
}
</script>

<style scoped>
.insurance {
  padding: 20px;
}

.card {
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.card-header {
  padding: 20px;
  border-bottom: 1px solid #ebeef5;
  background: #f5f7fa;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.title {
  font-size: 18px;
  font-weight: 600;
  color: #303133;
}

.filter-section {
  display: flex;
  align-items: center;
  gap: 10px;
}

.select {
  padding: 8px 12px;
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  background: white;
  min-width: 120px;
}

.select-sm {
  padding: 4px 8px;
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  background: white;
}

.btn {
  padding: 8px 16px;
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  background: white;
  cursor: pointer;
  transition: all 0.3s;
}

.btn:hover {
  background: #f5f7fa;
}

.btn-primary {
  background: #409eff;
  color: white;
  border-color: #409eff;
}

.btn-primary:hover {
  background: #66b1ff;
}

.btn-success {
  background: #67c23a;
  color: white;
  border-color: #67c23a;
}

.btn-success:hover {
  background: #85ce61;
}

.btn-sm {
  padding: 4px 8px;
  font-size: 12px;
}

.btn-text {
  background: transparent;
  border: none;
  color: #409eff;
}

.btn-text:hover {
  background: transparent;
  color: #66b1ff;
}

.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.table-container {
  padding: 20px;
}

.table {
  width: 100%;
  border-collapse: collapse;
}

.table th,
.table td {
  padding: 12px;
  text-align: left;
  border-bottom: 1px solid #ebeef5;
}

.table th {
  background: #f5f7fa;
  font-weight: 600;
  color: #606266;
}

.table-sm th,
.table-sm td {
  padding: 8px;
  font-size: 12px;
}

.loading {
  text-align: center;
  padding: 40px;
  color: #909399;
}

.pagination-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-top: 1px solid #ebeef5;
}

.pagination-info {
  color: #606266;
}

.pagination-controls {
  display: flex;
  align-items: center;
  gap: 10px;
}

.page-info {
  color: #606266;
  margin: 0 10px;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal {
  background: white;
  border-radius: 8px;
  width: 90%;
  max-width: 800px;
  max-height: 90vh;
  overflow-y: auto;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #ebeef5;
}

.modal-header h3 {
  margin: 0;
  color: #303133;
}

.close-btn {
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
  color: #909399;
}

.close-btn:hover {
  color: #606266;
}

.modal-body {
  padding: 20px;
}

.detail-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
  margin-bottom: 20px;
}

.detail-item {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.detail-item label {
  font-weight: 600;
  color: #606266;
  font-size: 14px;
}

.detail-item span {
  color: #303133;
}

.full-width {
  grid-column: 1 / -1;
}

.coverage-list {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.coverage-item {
  color: #606266;
}

.similar-section {
  border-top: 1px solid #ebeef5;
  padding-top: 20px;
}

.similar-section h4 {
  margin: 0 0 16px 0;
  color: #303133;
}

.form-group {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.form-group label {
  width: 100px;
  font-weight: 600;
  color: #606266;
}

.input {
  flex: 1;
  padding: 8px 12px;
  border: 1px solid #dcdfe6;
  border-radius: 4px;
}

.input:focus {
  outline: none;
  border-color: #409eff;
}

.highlight-primary {
  color: #409eff;
  font-weight: 600;
}

.highlight-success {
  color: #67c23a;
  font-weight: 600;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  padding-top: 20px;
  border-top: 1px solid #ebeef5;
}

.tag {
  display: inline-block;
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: 500;
}

.tag-info {
  background: #f4f4f5;
  color: #909399;
}

.tag-warning {
  background: #fdf6ec;
  color: #e6a23c;
}

.tag-success {
  background: #f0f9eb;
  color: #67c23a;
}

.rating {
  display: flex;
  align-items: center;
  gap: 8px;
}

.stars {
  display: flex;
}

.star {
  color: #dcdfe6;
  font-size: 14px;
}

.star.active {
  color: #f7ba2a;
}

.score {
  color: #909399;
  font-size: 12px;
}
</style>