<template>
  <div id="app">
    <el-container>
      <!--  -->
      <el-aside width="200px" style="background-color: #304156; min-height: 100vh;">
        <el-menu
          default-active="1"
          class="el-menu-vertical-demo"
          background-color="#304156"
          text-color="#bfcbd9"
          active-text-color="#409EFF"
          router
        >
          <div class="logo">
            <h2 style="color: white; text-align: center; padding: 20px 0;"></h2>
          </div>
          
          <el-sub-menu index="1">
            <template #title>
              <el-icon><location /></el-icon>
              <span></span>
            </template>
            <el-menu-item index="/dashboard"></el-menu-item>
            <el-menu-item index="/funds"></el-menu-item>
            <el-menu-item index="/stocks"></el-menu-item>
          </el-sub-menu>

          <el-sub-menu index="2">
            <template #title>
              <el-icon><setting /></el-icon>
              <span></span>
            </template>
            <el-menu-item index="/financial-diagnosis"></el-menu-item>
            <el-menu-item index="/insurance"></el-menu-item>
          </el-sub-menu>

          <el-menu-item index="/profile">
            <el-icon><user /></el-icon>
            <span></span>
          </el-menu-item>

          <el-menu-item index="/login" v-if="!isLoggedIn">
            <el-icon><login /></el-icon>
            <span></span>
          </el-menu-item>

          <el-menu-item index="/logout" v-if="isLoggedIn" @click="handleLogout">
            <el-icon><logout /></el-icon>
            <span></span>
          </el-menu-item>
        </el-menu>
      </el-aside>

      <!--  -->
      <el-container>
        <el-header style="background-color: #fff; border-bottom: 1px solid #e6e6e6;">
          <div class="header-content">
            <h3>{{ currentRouteName }}</h3>
            <div class="user-info" v-if="isLoggedIn">
              <el-avatar :size="32" :src="userAvatar"></el-avatar>
              <span style="margin-left: 10px;">{{ userName }}</span>
            </div>
          </div>
        </el-header>

        <el-main style="background-color: #f0f2f5; padding: 20px;">
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import { Location, Setting, User, Login, Logout } from '@element-plus/icons-vue'
import { mapState, mapActions } from 'vuex'

export default {
  name: 'App',
  components: {
    Location,
    Setting,
    User,
    Login,
    Logout
  },
  computed: {
    ...mapState(['isLoggedIn', 'userName', 'userAvatar']),
    currentRouteName() {
      const routeNames = {
        '/dashboard': '',
        '/funds': '',
        '/stocks': '',
        '/financial-diagnosis': '',
        '/insurance': '',
        '/profile': '',
        '/login': ''
      }
      return routeNames[this.$route.path] || ''
    }
  },
  methods: {
    ...mapActions(['logout']),
    handleLogout() {
      this.logout()
      this.$router.push('/login')
    }
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.logo {
  border-bottom: 1px solid #475669;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.user-info {
  display: flex;
  align-items: center;
}

.el-menu-vertical-demo {
  border-right: none;
}

.el-header {
  padding: 0 20px;
}

.el-main {
  min-height: calc(100vh - 60px);
}
</style>
