<template>
  <div class="profile">
    <el-row :gutter="20">
      <!--  -->
      <el-col :span="8">
        <el-card>
          <template #header>
            <div class="header-content">
              <span></span>
              <el-button type="primary" size="small" @click="editProfile">
                
              </el-button>
            </div>
          </template>

          <div class="user-info">
            <div class="avatar-section">
              <el-avatar :size="80" :src="userInfo.avatar" />
              <div class="user-name">
                <h3>{{ userInfo.name }}</h3>
                <p>{{ userInfo.role }}</p>
              </div>
            </div>

            <el-descriptions :column="1" border>
              <el-descriptions-item label="ID">
                {{ userInfo.id }}
              </el-descriptions-item>
              <el-descriptions-item label="">
                {{ userInfo.age }}
              </el-descriptions-item>
              <el-descriptions-item label="">
                {{ userInfo.occupation }}
              </el-descriptions-item>
              <el-descriptions-item label="">
                {{ userInfo.income.toLocaleString() }}
              </el-descriptions-item>
              <el-descriptions-item label="">
                <el-tag :type="getRiskLevelTagType(userInfo.riskLevel)">
                  {{ userInfo.riskLevel }}
                </el-tag>
              </el-descriptions-item>
              <el-descriptions-item label="">
                {{ userInfo.registerTime }}
              </el-descriptions-item>
            </el-descriptions>
          </div>
        </el-card>

        <!--  -->
        <el-card style="margin-top: 20px;">
          <template #header>
            <span></span>
          </template>
          <div class="asset-overview">
            <div class="asset-item">
              <div class="asset-icon total">
                <i class="el-icon-money"></i>
              </div>
              <div class="asset-info">
                <div class="asset-value">{{ totalAssets.toLocaleString() }}</div>
                <div class="asset-label"></div>
              </div>
            </div>
            <div class="asset-item">
              <div class="asset-icon profit">
                <i class="el-icon-trend-charts"></i>
              </div>
              <div class="asset-info">
                <div class="asset-value">+{{ totalProfit.toLocaleString() }}</div>
                <div class="asset-label"></div>
              </div>
            </div>
            <div class="asset-item">
              <div class="asset-icon rate">
                <i class="el-icon-data-line"></i>
              </div>
              <div class="asset-info">
                <div class="asset-value">{{ profitRate }}%</div>
                <div class="asset-label"></div>
              </div>
            </div>
          </div>
        </el-card>
      </el-col>

      <!--  -->
      <el-col :span="16">
        <el-card>
          <template #header>
            <span></span>
          </template>
          <div class="portfolio">
            <el-table :data="portfolio" style="width: 100%">
              <el-table-column prop="type" label="" width="100">
                <template #default="scope">
                  <el-tag :type="getProductTypeTagType(scope.row.type)">
                    {{ scope.row.type }}
                  </el-tag>
                </template>
              </el-table-column>
              <el-table-column prop="name" label="" width="200"></el-table-column>
              <el-table-column prop="amount" label="" width="120">
                <template #default="scope">
                  {{ scope.row.amount.toLocaleString() }}
                </template>
              </el-table-column>
              <el-table-column prop="currentValue" label="" width="120">
                <template #default="scope">
                  {{ scope.row.currentValue.toLocaleString() }}
                </template>
              </el-table-column>
              <el-table-column prop="profit" label="" width="100">
                <template #default="scope">
                  <span :class="scope.row.profit >= 0 ? 'profit-positive' : 'profit-negative'">
                    {{ scope.row.profit >= 0 ? '+' : '' }}{{ scope.row.profit.toLocaleString() }}
                  </span>
                </template>
              </el-table-column>
              <el-table-column prop="profitRate" label="" width="100">
                <template #default="scope">
                  <span :class="scope.row.profitRate >= 0 ? 'profit-positive' : 'profit-negative'">
                    {{ scope.row.profitRate >= 0 ? '+' : '' }}{{ scope.row.profitRate }}%
                  </span>
                </template>
              </el-table-column>
              <el-table-column prop="purchaseDate" label="" width="120"></el-table-column>
              <el-table-column label="" width="120">
                <template #default="scope">
                  <el-button type="text" @click="viewProductDetail(scope.row)"></el-button>
                  <el-button type="text" @click="sellProduct(scope.row)"></el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-card>

        <!--  -->
        <el-card style="margin-top: 20px;">
          <template #header>
            <span></span>
          </template>
          <div class="investment-history">
            <el-timeline>
              <el-timeline-item
                v-for="(record, index) in investmentHistory"
                :key="index"
                :timestamp="record.time"
                :type="record.type"
              >
                <div class="history-item">
                  <div class="history-content">
                    <span class="action">{{ record.action }}</span>
                    <span class="product">{{ record.product }}</span>
                    <span class="amount">{{ record.amount.toLocaleString() }}</span>
                  </div>
                  <div class="history-detail">
                    {{ record.detail }}
                  </div>
                </div>
              </el-timeline-item>
            </el-timeline>
          </div>
        </el-card>

        <!--  -->
        <el-card style="margin-top: 20px;">
          <template #header>
            <span></span>
          </template>
          <div class="recommendation-history">
            <el-table :data="recommendationHistory" style="width: 100%">
              <el-table-column prop="date" label="" width="120"></el-table-column>
              <el-table-column prop="type" label="" width="100">
                <template #default="scope">
                  <el-tag :type="getProductTypeTagType(scope.row.type)" size="small">
                    {{ scope.row.type }}
                  </el-tag>
                </template>
              </el-table-column>
              <el-table-column prop="product" label="" width="200"></el-table-column>
              <el-table-column prop="score" label="" width="100">
                <template #default="scope">
                  <el-rate
                    :model-value="scope.row.score"
                    disabled
                    show-score
                    text-color="#ff9900"
                    score-template="{value}"
                  />
                </template>
              </el-table-column>
              <el-table-column prop="status" label="" width="100">
                <template #default="scope">
                  <el-tag :type="getStatusTagType(scope.row.status)" size="small">
                    {{ scope.row.status }}
                  </el-tag>
                </template>
              </el-table-column>
              <el-table-column label="" width="120">
                <template #default="scope">
                  <el-button type="text" @click="viewRecommendation(scope.row)"></el-button>
                  <el-button 
                    type="text" 
                    @click="purchaseRecommendation(scope.row)"
                    v-if="scope.row.status === ''"
                  >
                    
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <!--  -->
    <el-dialog v-model="editDialogVisible" title="" width="500px">
      <el-form :model="editForm" label-width="100px">
        <el-form-item label="">
          <el-input v-model="editForm.name" />
        </el-form-item>
        <el-form-item label="">
          <el-input-number v-model="editForm.age" :min="18" :max="80" />
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="editForm.occupation" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input-number v-model="editForm.income" :min="0" :step="10000" />
          <span style="margin-left: 10px"></span>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="editForm.riskLevel">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <template #footer>
        <el-button @click="editDialogVisible = false"></el-button>
        <el-button type="primary" @click="saveProfile"></el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'Profile',
  data() {
    return {
      userInfo: {
        id: 'U10001',
        name: '',
        role: '',
        avatar: '',
        age: 35,
        occupation: '',
        income: 200000,
        riskLevel: '',
        registerTime: '2024-01-15'
      },
      totalAssets: 285000,
      totalProfit: 18500,
      profitRate: 6.95,
      portfolio: [
        {
          id: 1,
          type: '',
          name: '',
          amount: 50000,
          currentValue: 53500,
          profit: 3500,
          profitRate: 7.0,
          purchaseDate: '2024-03-10'
        },
        {
          id: 2,
          type: '',
          name: '',
          amount: 80000,
          currentValue: 92000,
          profit: 12000,
          profitRate: 15.0,
          purchaseDate: '2024-02-15'
        },
        {
          id: 3,
          type: '',
          name: '',
          amount: 150000,
          currentValue: 150000,
          profit: 0,
          profitRate: 0,
          purchaseDate: '2024-01-20'
        },
        {
          id: 4,
          type: '',
          name: '',
          amount: 5000,
          currentValue: 5100,
          profit: 100,
          profitRate: 2.0,
          purchaseDate: '2024-04-05'
        }
      ],
      investmentHistory: [
        {
          time: '2024-04-05 14:30',
          type: 'success',
          action: '',
          product: '',
          amount: 5000,
          detail: '3.5%'
        },
        {
          time: '2024-03-10 10:15',
          type: 'success',
          action: '',
          product: '',
          amount: 50000,
          detail: ''
        },
        {
          time: '2024-02-15 09:45',
          type: 'success',
          action: '',
          product: '',
          amount: 80000,
          detail: ''
        },
        {
          time: '2024-01-20 16:20',
          type: 'info',
          action: '',
          product: '',
          amount: 150000,
          detail: ''
        }
      ],
      recommendationHistory: [
        {
          id: 1,
          date: '2024-04-10',
          type: '',
          product: '',
          score: 4.2,
          status: ''
        },
        {
          id: 2,
          date: '2024-04-08',
          type: '',
          product: '',
          score: 4.0,
          status: ''
        },
        {
          id: 3,
          date: '2024-04-05',
          type: '',
          product: '',
          score: 3.8,
          status: ''
        },
        {
          id: 4,
          date: '2024-04-01',
          type: '',
          product: '',
          score: 4.5,
          status: ''
        }
      ],
      editDialogVisible: false,
      editForm: {
        name: '',
        age: 0,
        occupation: '',
        income: 0,
        riskLevel: ''
      }
    }
  },
  mounted() {
    this.loadUserData()
  },
  methods: {
    loadUserData() {
      // 
      console.log('')
    },
    
    editProfile() {
      this.editForm = { ...this.userInfo }
      this.editDialogVisible = true
    },
    
    saveProfile() {
      this.userInfo = { ...this.editForm }
      this.$message.success('')
      this.editDialogVisible = false
    },
    
    getRiskLevelTagType(level) {
      const typeMap = {
        '': 'info',
        '': 'success',
        '': 'warning',
        '': 'danger'
      }
      return typeMap[level] || 'info'
    },
    
    getProductTypeTagType(type) {
      const typeMap = {
        '': 'primary',
        '': 'success',
        '': 'warning',
        '': 'info'
      }
      return typeMap[type] || 'info'
    },
    
    getStatusTagType(status) {
      const typeMap = {
        '': 'success',
        '': 'info'
      }
      return typeMap[status] || 'info'
    },
    
    viewProductDetail(product) {
      this.$message.info(`${product.name}`)
    },
    
    sellProduct(product) {
      this.$confirm(`${product.name}`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$message.success(`${product.name}`)
      })
    },
    
    viewRecommendation(recommendation) {
      this.$message.info(`${recommendation.product}`)
    },
    
    purchaseRecommendation(recommendation) {
      this.$confirm(`${recommendation.product}`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'info'
      }).then(() => {
        recommendation.status = ''
        this.$message.success(`${recommendation.product}`)
      })
    }
  }
}
</script>

<style scoped>
.profile {
  padding: 20px;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.user-info {
  padding: 10px 0;
}

.avatar-section {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  padding: 0 10px;
}

.user-name {
  margin-left: 15px;
}

.user-name h3 {
  margin: 0 0 5px 0;
  color: #303133;
}

.user-name p {
  margin: 0;
  color: #909399;
  font-size: 14px;
}

.asset-overview {
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
}

.asset-item {
  display: flex;
  align-items: center;
  flex: 1;
  text-align: center;
}

.asset-icon {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 15px;
  font-size: 24px;
  color: white;
}

.asset-icon.total {
  background-color: #409EFF;
}

.asset-icon.profit {
  background-color: #67C23A;
}

.asset-icon.rate {
  background-color: #E6A23C;
}

.asset-value {
  font-size: 18px;
  font-weight: bold;
  color: #303133;
}

.asset-label {
  font-size: 14px;
  color: #909399;
}

.portfolio {
  padding: 10px 0;
}

.profit-positive {
  color: #67C23A;
  font-weight: bold;
}

.profit-negative {
  color: #F56C6C;
  font-weight: bold;
}

.investment-history {
  padding: 10px 0;
}

.history-item {
  padding: 5px 0;
}

.history-content {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5px;
}

.history-content .action {
  font-weight: bold;
  color: #303133;
}

.history-content .product {
  color: #409EFF;
}

.history-content .amount {
  color: #67C23A;
  font-weight: bold;
}

.history-detail {
  color: #606266;
  font-size: 14px;
}

.recommendation-history {
  padding: 10px 0;
}
</style>
